import { useState, useEffect } from "react";
import "./Portfolio.css";
import { MainMenu } from "./Header/MainMenu";
import { Rain } from "./Header/Rain";
import { ContentSection } from "./Contents/Contents";
import { ProjectSection } from "./Projects/Projects";
import { animateScroll as scroll } from "react-scroll";

const PortfolioBody = ({ renderPage }) => {
    const [links, setLinks] = useState(false);
    const [show, setShow] = useState(false);

    useEffect(() => {
        if (!links) {
            setTimeout(() => {
                setLinks(true);
            }, 5500);
        }
    });
    function renderBot() {
        setShow(true);
        scroll.scrollTo(document.body.clientHeight, {
            smooth: "easeInQuad",
            duration: 2000,
        });
    };
    function renderProject() {
        scroll.scrollTo(document.body.clientHeight * 2, {
            smooth: "easeInQuad",
            duration: 1000,
        });
    }
    return (
        <div className={"pf-body"}>
            <MainMenu links={links} renderBot={renderBot} />
            {show ? <ContentSection goToProject={renderProject} /> : ""}
            {show ? <ProjectSection renderPage={renderPage} /> : ""}
        </div>
    )
};

export default function Portfolio({ renderPage }) {
    const NUMBER_OF_RAINDROPS = 50;
    return (
        <div className={"pf-container"}>
            <Rain raindrops={NUMBER_OF_RAINDROPS} />
            <PortfolioBody renderPage={renderPage} />
        </div>
    )
}