import { useState, useEffect } from "react";
import "./ProjectList.css";
import { animateScroll as scroll } from "react-scroll";

const projectTexts = {
    pokemon: {
        title: "Pokemon Database v1.0",
        description: "List of pokemons retrieved by API calls."
    },
    yugioh: {
        title: "Yugioh Database v1.0",
        description: "List of yugioh cards retrieved by API calls.",
    }
}

const poolsCons = "bcdfghjklmnpqrstvwxyz".split("");
const poolsVows = "aeiou".split("");
const theLostLanguageGenerator = (str) => { 
    return str.split(" ").map((word) => {
        const sglQuote = word.length > 3 ? Math.round(Math.random() * 2) : 0;
        const randQuotes = Array.from(Array(sglQuote)).map(() => {
            const randQ = Math.ceil(Math.random() * word.length) - 1;
            return randQ <= 0 ? 0 : randQ;
        });
        return word.split("").map((c, i) => {
            if (randQuotes.includes(i)) return "'";
            const randVow = Math.ceil(Math.random() * 5) - 1;
            const randCon = Math.ceil(Math.random() * 21) - 1;
            const ltr = poolsVows.includes(c.toLowerCase()) ? 
                poolsVows[(randVow <= 0 ? 0 : randVow)] : 
                (
                    poolsCons.includes(c.toLowerCase()) ? 
                    poolsCons[(randCon <= 0 ? 0 : randCon)] : 
                    c
                );
            return i == 0 ? ltr.toUpperCase() : ltr;
        }).join("");
    }).join(" ");
}

export const ProjectList = ({ renderPage }) => {
    const projectList = {
        "pokemon": (key, r) => { 
            return <ProjectPokemon key={key} 
                renderPage = {r}
                lostTitle={theLostLanguageGenerator(projectTexts.pokemon.title)} 
                lostDescription={theLostLanguageGenerator(projectTexts.pokemon.description)} /> 
        },
        "yugioh": (key, r) => {
            return <ProjectYugioh key={key} 
                renderPage = {r}
                lostTitle={theLostLanguageGenerator(projectTexts.yugioh.title)} 
                lostDescription={theLostLanguageGenerator(projectTexts.yugioh.description)} />
        }
    }
    return (
        <div className={"projects-list-container"}>
            {Object.keys(projectList).map((k, i) => {
                return projectList[k](i, renderPage);
            })}
        </div>
    );
}

const LostLetter = ({ letter, duration }) => {
    const [display, setDisplay] = useState(letter);   
    useEffect(() => {
        let timer; 
        timer = setTimeout(() => {
                setDisplay(letter);
            }, duration);
        return () => clearTimeout(timer);
    }, [letter, duration]);
    return <div style={{ margin: letter == " " ? "5px"  : "" }}>{display}</div>
}

const LostWord = ({ lostTitle, decodedWord, hover }) => {
    return lostTitle.split("").map((c, i) => {
        return <LostLetter key={i} letter={hover ? decodedWord[i] : c} duration={i * 20} /> 
    })
}

const ProjectPokemon = ({ lostTitle, lostDescription, renderPage }) => {
    const [hover, setHover] = useState(false);
    function renderProject() {
        // scroll.scrollTo(0, {
        //     smooth: "easeInQuad",
        //     duration: 2000,
        // });
        renderPage("Pokemon");
    }
    return (
        <div className={"project-container"} 
            onMouseEnter={() => { setHover(true) }}
            onMouseLeave={() => { setHover(false) }}
            onClick={renderProject}>
            <div className={"project-header-container"}>
                <div className={"pokemon-icon-container"} />
                <div className={"project-title"}>
                    <LostWord lostTitle={lostTitle} decodedWord={projectTexts.pokemon.title} hover={hover} />
                </div>
            </div>
            <div className={"project-description"}>
                <LostWord lostTitle={lostDescription} decodedWord={projectTexts.pokemon.description} hover={hover} />
            </div>
        </div>
    )
}


const ProjectYugioh = ({ lostTitle, lostDescription, renderPage }) => {
    const [hover, setHover] = useState(false);
    return (
        <div className={"project-container"} 
            onMouseEnter={() => { setHover(true) }}
            onMouseLeave={() => { setHover(false) }}>
            <div className={"project-header-container"}>
                <div className={"yugioh-icon-container"} />
                <div className={"project-title"}>
                        <LostWord lostTitle={lostTitle} decodedWord={projectTexts.yugioh.title} hover={hover} />
                </div>
            </div>
            <div className={"project-description"}>
                <LostWord lostTitle={lostDescription} decodedWord={projectTexts.yugioh.description} hover={hover} />
            </div>
        </div>
    )
}