import { useState } from "react";
import { NavIcons } from "./Links";
import { NeonLetter } from "./Header";

export const MainMenu = ({ links, renderBot }) => {
    const [neon, setNeon] = useState("name");
    function changeNeon(letter) {
        setNeon(letter);
    };
    return (
        <div className={"main-menu-container"}>
            {links ? <NavIcons changeNeon={changeNeon} renderBot={renderBot} /> : ""}
            <NeonLetter linksOn={links} neon={neon} />
        </div>

    )
};